<template>
  <el-menu
    default-active="1-4-1"
    class="el-menu-vertical-demo"
    @open="handleOpen"
    @close="handleClose"
    :collapse="isCollapse"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
    <h3>{{ isCollapse ? "后台" : "后台管理系统" }}</h3>
    <el-menu-item
      :index="item.name"
      v-for="item in noChildren"
      :key="item.name"
      @click="clickMenu(item)"
    >
      <i :class="`el-icon-${item.icon}`"></i>
      <span slot="title">{{ item.label }}</span>
    </el-menu-item>

    <el-submenu
      :index="item.label"
      v-for="item in hasChildren"
      :key="item.label"
    >
      <template slot="title">
        <i :class="`el-icon-${item.icon}`"></i>
        <span slot="title">{{ item.label }}</span>
      </template>
      <el-menu-item-group
        :index="subItem.label"
        v-for="subItem in item.children"
        :key="subItem.label"
      >
        <el-menu-item :index="subItem.name" @click="clickMenu(subItem)">{{
          subItem.label
        }}</el-menu-item>
      </el-menu-item-group>
    </el-submenu>
  </el-menu>
</template>

<script>
import Cookies from "js-cookie";
import { mapGetters } from "vuex";
export default {
  data() {
    return { menuData: [] };
  },
  methods: {
    handleOpen(key, keyPath) {},
    handleClose(key, keyPath) {},

    clickMenu(item) {
      this.$router.push(item.path);
      this.$store.dispatch("addTabList", item);
    },

    getMenuData() {
      this.menuData = JSON.parse(Cookies.get("menu")) || this.$store.state.menu;
    },
  },

  computed: {
    noChildren() {
      return this.menuData.filter((item) => !item.children);
    },
    hasChildren() {
      return this.menuData.filter((item) => item.children);
    },
    ...mapGetters(["isCollapse"]),
  },

  beforeMount() {
    this.getMenuData();
  },
};
</script>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-menu {
  height: 100vh;
  border: none;
  h3 {
    letter-spacing: 2px;
    text-align: center;
    color: #fff;
    margin: 20px 0px;
  }
}
</style>