import Vue from 'vue'
import App from './App.vue'


//引入element-ui
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

//引入vueRouter
import router from '@/router';
//引入vuex
import store from '@/store';
//引入mock模拟数据
import '@/api/mock';


new Vue({
  router,
  store,
  render: h => h(App),

  mounted() {
    store.dispatch('registerRouter', router);
  }

}).$mount('#app')
