//home的vuex数据仓库
import Cookies from 'js-cookie';

//state:数据仓库
const state = {
    //初始化默认数据是服务器返回的类型  服务器返回对象初始值就写{} 服务器返回数组就写[]
    isCollapse: false,
    tabList: [
        {
            path: "/",
            name: "home",
            label: "首页",
            icon: "s-home",
            url: "Home",
        }
    ],
    menu: []
};

//mutation:修改state的唯一手段
const mutations = {
    TOGGLECOLLAPSE(state) {
        state.isCollapse = !state.isCollapse;
    },
    ADDTABLIST(state, value) {
        state.tabList.push(value);
    },
    CHANGEBREADCRUMBITEM(state, value) {
        state.tabList = value;
    },
    SETMENU(state, value) {
        state.menu = value;
    }
};

//可以在修改数据前 做一些操作 比如异步操作 发送axios请求
const actions = {
    toggleCollapse({ commit }) {
        commit('TOGGLECOLLAPSE');
    },
    addTabList({ commit, state }, value) {
        if (value.name == 'home' || value.name == '/') {//如果点击的是主页就重置面包屑
            state.tabList = [];
            commit('ADDTABLIST', value);
        } else if (state.tabList.findIndex(element => { return value == element }) == -1) {
            //如果点击的内容面包屑中没有 那么就添加
            commit('ADDTABLIST', value);
        } else {
            //如果点击的内容面包屑中有 并且点击的元素后方还有节点 那么就截取从首页到用户点击的节点
            let index = state.tabList.findIndex(element => { return value == element })
            commit('CHANGEBREADCRUMBITEM', state.tabList.slice(0, index + 1));
        }
    },
    initTabList({ state }) {
        state.tabList = [{
            path: "/",
            name: "home",
            label: "首页",
            icon: "s-home",
            url: "Home",
        }];
    },
    setMenu({ commit }, value) {
        commit('SETMENU', value);
        Cookies.set('menu', JSON.stringify(value));
    },

    //设置不同权限的路由
    //动态路由
    registerRouter(state, router) {

        if (!Cookies.get('menu')) return;
        const menu = JSON.parse(Cookies.get('menu'))
        state.menu = menu;

        const registerArry = [];
        menu.forEach(item => {
            console.log(item, 'item');
            if (item.children) {
                item.children = item.children.map(item => {
                    item.component = () => import(`@/views/${item.url}`)
                    return item
                })
                registerArry.push(...item.children)
            } else {
                item.component = () => import(`@/views/${item.url}`)
                registerArry.push(item)
            }
        });
        registerArry.forEach((item) => {
            router.addRoute('main', item)
        })
    }


};

//getters:计算属性 用于简化仓库数据 让组件 获取仓库的数据方便
const getters = {
    isCollapse(state) {
        return state.isCollapse;
    },
    tabList(state) {
        return state.tabList;
    },
};

export default {
    state,
    mutations,
    actions,
    getters
};





