<template>
  <div class="header-container">
    <div class="l-container">
      <el-button icon="el-icon-menu" size="mini" @click="toggleCollapse"></el-button>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item @click.native="clickMenu(item)" v-for="item in tabList" :key="item.path">{{ item.label }}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="r-container">
      <el-dropdown @command="handelClick">
        <span class="el-dropdown-link">
          <img class="user-avatar" src="@/assets/images/user.jpg" alt="" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item command="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Cookie from "js-cookie";

export default {
  data() {
    return {};
  },
  methods: {
    toggleCollapse() {
      this.$store.dispatch("toggleCollapse");
    },
    clickMenu(item) {
      this.$router.push(item.path);
      this.$store.dispatch("addTabList", item);
    },
    handelClick(command) {
      if (command == "logout") {
        Cookie.remove("token");
        Cookie.remove("menu");
      }

      this.$router.replace("/login");
    },
  },
  computed: {
    ...mapGetters(["tabList"]),
  },

  mounted() {
    console.log(this.$router);
    console.log(this.$route);
    this.$store.dispatch("initTabList");
  },
};
</script>


<style lang="less" scoped>
.header-container {
  width: 100%;
  height: 64px;

  background-color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .l-container {
    padding-left: 20px;
    display: flex;
    align-items: center;

    span {
      color: aliceblue;
      margin-left: 10px;
      font-size: 14px;
    }

    .el-breadcrumb__item {
      /deep/.el-breadcrumb__inner {
        font-weight: normal;

        &.is-link {
          color: #fdfbfb;
        }
      }

      &:last-child {
        /deep/.el-breadcrumb__inner {
          color: #fff;
          font-weight: bolder;
        }
      }
    }
  }

  .r-container {
    padding-right: 20px;

    .user-avatar {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      vertical-align: middle;
    }
  }
}
</style>
